import React, {Component} from 'react';
import Hero from './subcomponentes/Hero.js'
import Info from './subcomponentes/Info.js'
import ExpZenior from './subcomponentes/ExpZenior.js'
import './../css/Body.css'

class Body extends Component{
  render () {
    return (
     <div className="Body w-100">
       <Hero className="w-100" />
       <Info className="w-100" />
       <ExpZenior className="w-100" />
     </div>
    );
  }
}

export default Body;
