import React, {Component} from 'react';
import {Navbar, Nav} from 'react-bootstrap';
import { Link } from "react-router-dom";
import logo from './../img/zenior-logo-blanco.png'
import 'bootstrap/dist/css/bootstrap.min.css';
import './../css/Header.css';


class Header extends Component{
  render () {
    return (
     <div className="Header">
      <Nav className="justify-content-end" activeKey="/r">
        <Nav.Item>
          <Nav.Link className="text-white mr-3">        <img
             alt="ZENIOR"
             src={logo}
             width="50"
             height="45"
        /></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className="text-white p-3" ><Link className="text-white" to="/"> SENIORS</Link></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className="text-white p-3"><Link className="text-white" to="/Empresas">EMPRESAS / INSTITUCIONES </Link></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className="text-white p-3" eventKey="link-3">Â¿QUIENES SOMOS?</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className="text-white p-3" eventKey="link-2">BLOG</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className="text-white p-3" eventKey="disabled">
            CONTACTO
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className="text-info p-3 bg-white" eventKey="disabled">
            INGRESAR AL CURSO
          </Nav.Link>
        </Nav.Item>
      </Nav>
     </div>
    );
  }
}

export default Header;
